// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
//     https://www.htmlcsscolor.com/hex/334482

$color-neon-blue:                   #2c39f2;
$color-resolution-blue:             #1c3374;
$color-torch-red:                   #ff1553;
$color-sapphire:                    #102654;
$color-torch-red:                   #ed0233;
$color-cobalt:                      #003eb7;
$color-ghost-white:                 #f8f9fc;
$color-crimson:                     #d10a42;
$color-chambray:                    #495a7d;
$color-light-slate-grey:            #74819c;
$color-egyptian-blue:               #103aae;
$color-persian-blue:                #2e35ac;
$color-smalt:                       #00359f;
$color-cobalt:                      #0041ca;
$color-whisper:                     #e8e8e8;
$color-link-water:                  #c3c8d4;
$color-dark-gray:                   #afafaf;
$color-spindle:                     #b6bdcc;
$color-black-russian:               #030a1a;
$color-hawkes-blue:                 #d4d7e3;

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

//    html   
$fz-default:                        16px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13

$ff-roboto-bold:                    "RobotoCondensed-Bold", "Arial", sans-serif;
$ff-roboto-regular:                 "RobotoCondensed-Regular", "Arial", sans-serif;
$ff-roboto-light:                   "RobotoCondensed-Light", "Arial", sans-serif;
$ff-alumni-extra-bold:              "AlumniSans-ExtraBold", "Arial", sans-serif;
$ff-alumni-bold:         	        "AlumniSans-Bold", "Arial", sans-serif;
$ff-alumni-semi-bold:               "AlumniSans-SemiBold", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

//      $tf-default,     
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1200:                           1200px;
$vp-1280:                           1280px;
$vp-1366:                           1366px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
